@import url(https://fonts.googleapis.com/css?family=Asap|Libre+Barcode+39+Text);
body {
  margin: 0;
  padding: 0;
  background-color: #f08080;
}
#app {
  margin-top: 60px;
}
.clocks {
  display: -ms-flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

#clock {
  color: #8b0000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 20px;
  position: relative;
}
#clock:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
    border-radius: 500px;
    -webkit-box-shadow: 0px 0px 15px #ddd inset, 0px 0px 15px rgba(0, 0, 0, 0.3);
            box-shadow: 0px 0px 15px #ddd inset, 0px 0px 15px rgba(0, 0, 0, 0.3);
    border: 2px solid #b22222;
}
.clock {
  position: relative;
  width: 400px;
  height: 400px;
  border-radius: 50%;
}
.clock::before {
    z-index: 3;
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #8b0000;
    border-radius: 50%;
}
.clock .steps {
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -3px;
    height: 100%;
    width: 4px;
}
.clock .steps::before, .clock .steps::after {
      content: "";
      position: absolute;
      left: -1px;
      width: 7px;
      height: 7px;
      background: rgba(139, 0, 0, 0.7);
      border-radius: 50%;
}
.clock .steps::before {
      top: 0.5rem;
}
.clock .steps::after {
      bottom: 0.5rem;
}
.clock .steps:nth-child(2) {
      -webkit-transform: rotate(30deg);
              transform: rotate(30deg);
}
.clock .steps:nth-child(3) {
      -webkit-transform: rotate(60deg);
              transform: rotate(60deg);
}
.clock .steps:nth-child(4) {
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
}
.clock .steps:nth-child(5) {
      -webkit-transform: rotate(120deg);
              transform: rotate(120deg);
}
.clock .steps:nth-child(6) {
      -webkit-transform: rotate(150deg);
              transform: rotate(150deg);
}
.clock .digits {
    position: absolute;
    bottom: 5rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-family: "Asap", sans-serif;
    text-transform: uppercase;
    font-size: 0.85em;
    opacity: 0.8;
    text-align: center;
}
.clock .display {
    position: absolute;
    top: 6rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-family: "Libre Barcode 39 Text", cursive;
    font-size: 2.6em;
    opacity: 0.5;
}
.clock .hour,
  .clock .min,
  .clock .sec {
    z-index: 2;
    position: absolute;
    bottom: 50%;
    left: 50%;
    height: 40%;
    width: 4px;
    margin-left: -2px;
    background: #e1f2ea;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
    border-radius: 3px 3px 0 0;
}
.clock .hour {
    height: 30%;
}
.clock .sec {
    width: 2px;
    z-index: 1;
    height: 46%;
    background: #8b0000;
}
@media screen and (max-width: 480px) {
.clock {
    width: 300px;
    height: 300px;
}
.clock .brand {
      bottom: 3rem;
}
.clock .display {
      top: 4.5rem;
      font-size: 2.2em;
}
}
